.TodoItem {
    background-color: #FAFAFA;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32,35,41, 0.15);
}

.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

.Icon{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
    /* font-size: 20px;
    font-weight: bold; */
    
}

.Icon-check{
    position: absolute;
    left: 12px;
}

.Icon-check--activate{
    color:green;
}

.Icon-delete{
    position: absolute;
    top: -15px;
    right: 0;
}

.Icon-delete:hover{
    color:red;
}